@media only screen and (min-width: 500px) {
  .module1 {
    border: 2px solid white;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    background-color: red;
    align-items: center;
    width: 80vw;
    overflow: hidden;
  }
  .module1 .loadersSec {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .module1 .loadersSec p {
    color: yellow;
    font-size: 8rem;
    background: rgba(100, 94, 94, 0.637);
  }
  .module1 .loadersSec progress {
    color: yellow;
  }
}

@media only screen and (max-width: 500px) {
  .module1 {
    border: 4px white solid;
    border-radius: 30px;
    height: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  .module1 .loadersSec {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .module1 .loadersSec p {
    color: yellow;
    font-size: 3rem;
    background: rgba(100, 94, 94, 0.637);
  }
  .module1 .loadersSec progress {
